.open_search {
  position: fixed;
  right: 25px;
  bottom: 25px;
}

.open_search a {
  display: block;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: #e60062;
  background-image: url("../../../icons/add.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 28px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  font-size: 0;
}
